.form {
  padding-left: rem(20);
  padding-right: rem(20);
  text-align: center;
  &__share {
    display: flex;
    justify-content: space-between; 
    margin-bottom: rem(30);  
  }
  &__legend {
    @include form-title;
    &--icon {
      @include icons;
    }
    &--arrow {
      @include arrow__expand;
    }
  }
  &__box {
    background-color: $color-orange;
    border-radius: rem(12);
    padding: rem(13) rem(18) rem(13) rem(17);
    display: inline-flex;
    align-self: center;
    margin-bottom: rem(30);
    &--button {
      background-color: $color-orange;
      font-family: $font-Open;
      text-transform: uppercase;
      color: $color-whireOriginal;
      border: none;
      margin-left: rem(5);      
    }
    &--icon {
      color: $color-whireOriginal;
    }
  }
}
